// components/Navbar.tsx
"use client"; // This directive makes it a client component

import { useState } from 'react';
import Link from 'next/link';
import styles from './Navbar.module.css'; // We'll create this file next
import { FaBars, FaTimes } from 'react-icons/fa';

// Define the structure of a nav item
interface NavItem {
  label: string;
  href: string;
}

const navItems: NavItem[] = [
  { label: 'Home', href: '/' },
  { label: 'About', href: '/about' },
  { label: 'Services', href: '/services' },
  { label: 'Contact', href: '/contact' },
];

export default function Navbar() {
  // State to manage mobile menu visibility
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  return (
    <nav className={styles.navbar}>
      <div className={styles.navContainer}>
        {/* Logo */}
        <Link href="/" className={styles.logo}>
          MyApp
        </Link>

        {/* Mobile Menu Icon */}
        <div className={styles.mobileIcon} onClick={toggleMobileMenu}>
          {isMobileMenuOpen ? <FaTimes /> : <FaBars />}
        </div>

        {/* Desktop Navigation Links */}
        <ul className={styles.navMenu}>
          {navItems.map((item) => (
            <li key={item.href}>
              <Link href={item.href} className={styles.navLink}>
                {item.label}
              </Link>
            </li>
          ))}
        </ul>

        {/* Mobile Navigation Menu */}
        <ul
          className={
            isMobileMenuOpen
              ? `${styles.navMenuMobile} ${styles.active}`
              : styles.navMenuMobile
          }
        >
          {navItems.map((item) => (
            <li key={item.href}>
              <Link
                href={item.href}
                className={styles.navLinkMobile}
                onClick={toggleMobileMenu} // Close menu on link click
              >
                {item.label}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </nav>
  );
}